<template>
  <div id="page-wrap" v-if="product">
    <div id="img-wrap">
      <img v-bind:src="product.imageUrl" />
    </div>
    <div id="product-details">
      <h1>{{ product.name }}</h1>
      <h3 id="price">${{ product.price }}</h3>
      <p>Average rating: {{ product.averageRating }}</p>
      <button 
      id="add-to-cart"
      v-if="!showSuccessMessage && !isItemInCart"
      v-on:click="addToCart">
      Add to Cart</button>
      <button 
      id="add-to-cart"
      class="green-button"
      v-if="showSuccessMessage"
      >
      Successfully addeditem to cart!</button>
      <button 
      id="add-to-cart"
      class="greyout-button"
      v-if="!showSuccessMessage && isItemInCart"
      >
      Already In the Cart</button>      
      <h4>Description</h4>
      <p>{{ product.description }}</p>
    </div>
  </div>
  <NotFoundPage v-else/>
</template>

<script>
import axios from 'axios';
// import {products} from '../fake-data'
import NotFoundPage from './NotFoundPage'
export default {
    name:'ProductDetailPage',
    components:{
      NotFoundPage
    },
    data(){
      return{
        // product: products.find((p) =>p.id ===this.$route.params.id)
        product:{},
        showSuccessMessage:false,
        cartItems:[]
      }
    },
    methods:{
      async addToCart(){
        await axios.post('/api/users/12345/cart',{
          productId: this.$route.params.id
        });
        this.showSuccessMessage = true;
        setTimeout(()=>{
          this.$route.push('/products');
        },1500)
        
      }
    },
    computed:{
      isItemInCart(){
        return this.cartItems.some(item => item.id ===this.product.id);
        // return (Object.values(this.cartItems).filter(item => item.id === this.product.id)).length ===1;
      }
    },
    async created() {

      const {data:product} = await axios.get(`/api/products/${this.$route.params.id}`);
      this.product = product;

      // const cartResult = await axios.get('/api/users/12345/cart');
      // const cartItems = cartResult.data;
      const {data: cartItems} =await axios.get('/api/users/12345/cart');
      this.cartItems = cartItems;
      // console.log(this.cartItems);
      // console.log((this.cartItems.filter((item)=> item.id === this.product.id)).length==1)
      // console.log((this.cartItems.filter((item)=> item.id === this.product.id)))
      // console.log((Object.values(this.cartItems).filter(item => item.id === this.product.id)).length ===1);
}}
</script>


<style scoped>
  #page-wrap {
    margin-top: 16px;
    padding: 16px;
    max-width: 600px;
  }

  #img-wrap {
    text-align: center;
  }

  img {
    width: 400px;
  }

  #product-details {
    padding: 16px;
    position: relative;
  }

  #add-to-cart {
    width: 100%;
  }

  .greyout-button{
    background-color: grey;
    pointer-events: none;
    cursor: default;

  }
  #price {
    position: absolute;
    top: 24px;
    right: 16px;
  }

  .green-button{
    background-color: green;
  }
</style>